body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Michroma-Italian';
  font-size: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.bigContainer{
    height: 100px;
    border-bottom: solid 1px lightgray;
}

#img-container{
    width: 27%;
    margin-right: 10%;
}

.img-fluid{
    width: 100%;  
}


#bootstrap-overrides .nav-link
{
    text-align: center;
    font-size: 15px;
    display: inline-block;
    font-family: 'Roboto-Black', sans-serif;
    
    margin-left: 3px; margin-right: 3px;
    color:  #035a9b;
    height: 30px;
}



.navbar{
    width: 60%;
    margin: auto;
    margin-top: 12px;
    margin-bottom: 0;
}

.btn{
    margin-top: 5px;
    border-radius: 3px;
}

#account-info{
    border: solid 2px #171717;
    border-radius: 3px;
    height: 57px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    color: #3D4849;
}

#account-info:hover{
    background-color: lightgray;
}

#account-dropdown .dropdown-menu .dropdown-item{
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0;
    margin-left: 0; margin-right: auto;
}
#account-dropdown .dropdown-menu .dropdown-item:hover{
    background-color: #01adef;
    color: white;
}

#account-dropdown .dropdown-menu .dropdown-item svg:hover{
    fill: red;
}

#account-dropdown:hover .dropdown-menu{
    display: block;
    text-decoration: none;
}

#display-info{
    margin: 0 5px 0 3px;
    text-align: left;
}



@media only screen and (max-width: 1577px){
    .navbar{
        width: 75%;
    }
}

@media only screen and (max-width: 1356px){
    .navbar{
        width: 100%;
    }
}

@media only screen and (max-width: 991px){
    .bigContainer{
        height: 10%;
    }
