/* Sidebar */

#welcome {
    padding-top: 10px;
    padding-bottom: 8%;
    border-bottom: solid 1px gray;
    height: 7%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

#menu-icon {
    width: 55px;
    height: 47px; 
    padding: 3px;
    border: solid 3px #3D4849;
    border-radius: 3px;
    margin-left: 7px;
}

#welcome-message{

    width: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
    margin: 0;
}

#welcome-message p{
    font-size: 24px;
    margin: 0;
    text-align: left;
}



/*Project List utility bar*/
#project-header{
    border-bottom: solid 1px gray;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
}

#change-view{
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
}

#bootstrap-override{
    display: flex;

}

#search{
    display: flex;
    justify-content: center;
    align-items: center;
}

#search Form{
    padding-right: 5px;
    margin: 0;
    width: 60%;
}
#filterBy{
    border: solid 3px  #3D4849;
    float: left;
    flex: 20%;
    color: #3D4849;
}


@media only screen and (max-width: 1300px){
  #welcome-message p{
      text-align: center;
      width: 100%;
  }
  #search Form{
    width: 40%;
}
}

@media only screen and (max-width: 1160px){
    #bootstrap-override{
        width: 70%;
    }
    #search Form{
        width: 30%;
    }
    #filterBy{
        max-width: 100%;
    }

}

@media only screen and (max-width: 992px){
    #bootstrap-override{
        width: 100%;
    }
    #search Form{
        width: 50%;
    }
    #filterBy{
        max-width: 100%;
    }
}
@media only screen and (max-width: 614px){
    
    #project-header, Table, #card-row{
        width: 100%;
        padding: 0;
        
    }
    #search Form{
        width: 80%;
    }

  
}

@media only screen and (max-width: 454px){
    #bootstrap-override .pill-1 .nav-link,
    #bootstrap-override .pill-2 .nav-link{
       width: 275px;
   }
   
}
/*Pills and Pill Toggle*/
.nav-pills{
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-pills .nav-item .nav-link{
    height: 50px;
    width: 125px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1px;
    border: 3px solid #3D4849;
}

#bootstrap-override .pill-1 .nav-link
{
    background-color: #00315c;
    color: white;
    
}

#bootstrap-override .pill-2 .nav-link
{
    background-color:   #00315c;
    color: white;
    
}

#bootstrap-override .pill-1 .nav-link:not(.active){
    background-color: white;
    color: black;
}

#bootstrap-override .pill-2 .nav-link:not(.active){
    background-color: white;
    color: black;
}




/*----------------------------------------*/

/*Individual Cards*/
#preview-img{
    width: 80%;
    margin: auto;
    display: block;
    margin-bottom: 7px;
}

#vessel-info{
    border: solid 3px #3D4849;
    margin: 5px;
    width: 24%;
}

#vessel-info .card-body .card-text{
    text-align: left;
    margin-bottom: 1px;
    color: #3D4849;
}

#vessel-info:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.5);
    text-decoration: none;
}

#card-row{
    height: 20%;
    clear: both;
}

/*Responsive Controls*/
@media only screen and (max-width: 1358px){
    #vessel-info{
        width: 35%;
    }
    #card-row{
        display: flex;
        justify-content: center;
    }

}

@media only screen and (max-width: 767px){
    #vessel-info{
        width: 80%;
    }
    #card-row{
        display: flex;
        justify-content: center;
    }
    
}





